import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'StartsWith'})
export class StartsWithPipe implements PipeTransform {
  transform(value: any[], term: string): any[] {
   let nom="3°A"
    console.log('term : '+term);
    console.log('startsWith : '+nom.toLowerCase().startsWith(term.toLowerCase()));

    console.log('value : '+JSON.stringify(value.filter((x:any) =>x.classe.nom.toLowerCase().startsWith(term.toLowerCase()))));
    
    return value.filter((x:any) =>x.classe.nom.toLowerCase().startsWith(term.toLowerCase()))

  } 
}